.postViewer{
    height: 98vh;
    width: 94vw;
    top: 1vh;
    left: 2vw;
    position: fixed;
    background-color: #101010;
    box-shadow: 0 4px 8px black;
    z-index: 5;
    box-sizing: border-box;
    margin: 0;
    resize: both;
    overflow: hidden;
    overflow-y: auto;
    padding: 20px 0 5px 0;
    cursor: move;
    z-index: 10;
    color: white;    
    border-radius: 5px;
}
.postViewer iframe{
    height: calc(100% - 40px);
    width: 100%;
}
.postViewer img{
    height: calc(100% - 20px);
    width: 100%;
    object-fit: contain;
}
.postViewer video{
    height: calc(100% - 20px);
    width: 100%;
    object-fit: contain;
}
.linkPreview img{
    height: 100%;
}