.pageCreationWindow{    
    width: 800px;
    padding: 10px;
    box-sizing: border-box;
    background-color: rgb(31, 35, 54);
    box-shadow: 0 5px 10px black;
    border-radius: 5px;
    position: fixed;
    top: calc(100px);
    left: calc(50% - 400px);
    cursor: auto;
    z-index: 3;
}
.pageCreationWindow input{
    height: 30px;
    width: 90%;
    margin: 5px;
}
.preview{
    height: 100px;
    width: 100px;
    display: inline-block;
    margin: 10px;
}
.preview img{
    height: 100%;
    width: 100%;
    object-fit: contain;
}
.postCreationInputSide{
    float: right;
    width: calc(50% - 10px);
}
.postCreationPreviewSide{
    float: left;
    width: calc(50% - 10px);
    height: 100%;
}
.postCreationButtons{
    width: 100%;
}
.postCreationButtons button{
    width: calc(45% - 5px);
}