.forum{
    background-color: #36393f;    
}
.forumHeader{    
    color: rgba(255, 255, 255, 0.747);
    padding-top: 60px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    box-shadow: 0 0px 10px black;
    background-color: #36393f;    
    margin-bottom: 10px;
    font-size: 16px;
}
.messagesContainer{
    text-align: center;
    width: 100%;   
    overflow-x: hidden;    
    overflow-y: auto;
    height: calc(100vh - 195px);
    padding-top: 10px;
    background-color: #36393f;
    padding-left: 30px;
    box-sizing: border-box;
}
.messageBox{
    border-radius: 5px;
    padding: 10px;
    box-sizing: border-box;
    color: rgba(255, 255, 255, 0.74);    
    width: 80%;
    display: inline-block;    
    position: relative;
    text-align: left;
    border-bottom: 1px solid rgba(128, 128, 128, 0.3);
    padding-left: 40px;    
}
.messageUser{
    color: blue;    
    font-weight: bold;
    display: inline-block;
    margin-right: 10px;
}
.messageDate{
    color: rgba(128, 128, 128, 0.644);
    display: inline-block;
}
.message{
    padding: 10px 10px 10px 0;    
}
.messageInputArea{
    position: absolute;
    bottom: 0;
    width: 100vw;
    min-height: 60px;
    padding: 20px 10px 20px 40px;
    box-sizing: border-box;    
    background-color: #36393f;
    text-align: center;
}
.messageInputBox{
    width: 80vw;
    display: inline-block;
    position: relative;    
    background-color: rgba(128, 128, 128, 0.185);
    color: white;
    box-sizing: border-box;
    padding: 10px;
    border-radius: 5px;
    font-size: 16px;      
    text-align: left;
}
.inputBox{
    width: calc(100% - 80px);    
    border: 1px solid #ccc;
    max-height: 150px;
    overflow-x: hidden;
    overflow-y: auto; 
    color: white; 
    border: none;
    outline: none;
    display: inline-block;
}
.sendButton{
    height: 40px;
    display: inline-block;
    padding: 5px 10px;
    border-radius: 5px;     
    position: absolute;    
    right: 5px;
    bottom: calc(50% - 25px);
    cursor: pointer;
}
.sendButton img{
    height: 100%;
    width: 100%;
}