.confirmationBox{
    position: fixed;
    width: 450px;    
    left: calc(50% - 200px);
    top: calc(50% - 200px);
    background-color: rgb(30, 32, 34);
    color: white;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0 5px 10px black;       
    box-sizing: border-box;
}
.message{
    padding-bottom: 20px;
}
.confirmationBox button{
    width: 45%;
}