html{
  background-color: #242424;
}
.App {
  text-align: center;  
}
.closeButton{
  height: 20px;
  line-height: 20px;
  width: 20px;
  background-color: rgb(216, 110, 110);
  color: white;
  font-size: 14px;
  cursor: pointer;
  position: absolute;
  top: 5px;
  right: 5px;
  border-radius: 3px;
  text-align: center;
}
.closeButton:hover{
  background-color: rgb(248, 186, 186);
  box-shadow: 0 0 10px rgb(216, 110, 110);
}
.blobTest{
  margin-top: 100px;
  min-height: 300px;
  background-color: gray
}
button{
  width: 200px;
  height: 40px;
  border: none;
  outline: none;
  border-radius: 2px;
  background-color: rgb(150, 150, 150);
  background-image: linear-gradient(rgba(150, 150, 150, 0), rgb(80, 80, 80));
  box-shadow: 0 5px 10px black;
  cursor: pointer;
  transition: 500ms;
  color: white;    
  margin-top: 15px;
  vertical-align: top;
}
button:hover{
  background-color: rgb(174, 174, 174);
  box-shadow: 0 3px 5px black;
  transform: scale(0.99);
}
input {
  outline: none;
  border: none;
  background-color: rgba(169, 169, 169, 0.455);
  color: white;
  padding: 0 10px;
  box-sizing: border-box;
  height: 100%;
}
select {
  outline: none;
  border: none;
  background-color: rgba(169, 169, 169, 0.455);
  color: white;
  padding: 0 10px;
  box-sizing: border-box;  
  width: 90%;
  height: 30px;
  margin: 5px;
}
.blur{
  filter: blur(40px);
}
.blue{
  background-color: blue;
  box-shadow: 0 10px 10px blue;
  border: 1px solid blue;
}