.listViewer{
    position: fixed;
    top: 10px;
    left: 10px;
    height: 90vh;
    width: 90vw;
    background-color: #161616;  
    margin-left: 5vw;
    margin-top: 6vh;
    box-shadow: 0 5px 10px black;
    border-radius: 5px;
    text-align: center;
    overflow-y: auto;  
    z-index: 4;
}
.listViewer input{
    display: inline-block;
    position: relative;
    height: 30px;
    float: none;
    text-align: center;
    margin: 0 5px;
}
.topMenu{
    width: 100%;
    text-align: center;
    padding: 20px;
    box-sizing: border-box;
}
.topMenu textarea{
    width: 90%;
    margin: 10px;
    padding: 10px;
    box-sizing: border-box;
    outline: none;
    border: none;
    background-color: rgba(255, 255, 255, 0.507);
    box-shadow: 0 5px 5px inset black;
    text-align: center;
}
.listViewer button{
    height: 30px;
    margin: 0 5px;
}
.listViewer .postButton{
    width: 400px;
    height: auto;
}
.listPostButton{
    overflow: hidden;
}