.adminContainer{
    padding-top: 80px;
}
.userTile{
    width: 300px;
    min-height: 100px;
    box-shadow: 0 5px 10px black;
    margin: 10px;
    display: inline-block;
    border-radius: 5px;
    cursor: pointer;
    transition: 250ms;
    padding: 10px;
    box-sizing: border-box;
    color: white;        
}
.userTile:hover{
    background-color: rgba(255, 255, 255, 0.089);
    box-shadow: 0 10px 20px black;
    transform: scale(1.01);
}
.userTileTitle{
    color: white;        
    line-height: 20px;
    margin-bottom: 10px;
}
.expandedUserTile{
    width: 95%;
}
.expandedUserTile:hover{
    background-color: rgba(128, 128, 128, 0);
    box-shadow: 0 5px 10px black;    
    transform: none;
}
.userTile .playButton{
    height: 50px;
    top: calc(50% - 30px);
    vertical-align: top;
}
.adminPost{
    display: inline-block;
}
.adminPostText{
    margin: 0 10px;
    display: inline-block;
}
.topPost{
    border: 1px solid black;
    background-color: rgba(128, 128, 128, 0.384);
    box-shadow: 0 5px 10px black;
    display: inline-block;
    border-radius: 5px;    
    margin: 5px;
}
.adminSection{
    width: 95%;
    box-shadow: 0 5px 10px black;
    padding: 10px;
    margin: 10px;
    color: white;
    display: inline-block;
    cursor: pointer;
}
.adminSectionTitle{
    user-select: none;
}
.hidden{
    display: none;
}
.linkDisplay{
    display: inline-block;
    background-color: rgba(128, 128, 128, 0.507);
    box-shadow: 0 3px 6px black;
    border-radius: 5px;    
    padding-top: 5px;
    margin: 5px;
    transition: 500ms;
}
.linkDisplay:hover{
    transform: scale(1.01);
    box-shadow: 0 6px 12px black;
}
.linkDisplayText{
    padding: 5px;
    max-width: 200px;
    overflow: hidden;
}
.chartArea{
    cursor: auto;
}
.chartTitle{    
    margin: 20px;
    user-select: none;
}
.adminPostViwer{
    width: 300px;
    display: inline-block;
    margin: 5px;
    position: relative;
}
.editIcon{
    position: absolute;
    top: 15px;
    right: 10px;
    height: 30px;
    width: 30px;
    z-index: 3;
}
.editIcon img{
    height: 100%;
    width: 100%;
}
.postCreationMessage{
    color: white;
}