.addToListMenu{
    position: fixed;
    padding: 20px 0 10px 0;
    width: 500px;
    border-radius: 5px;
    background-color: black;
    top: calc(50% - 150px);
    left: calc(50% - 250px);
    color: white;
    text-align: center;
    z-index: 10;
}
.addListSectionDescription{
    margin-bottom: 10px;
}
.addToListMenuInput{
    height: 40px;
    width: 200px;    
    vertical-align: top;
    margin: 0;
}
.addListSection{
    display: inline-block;
    margin: 10px;
    text-align: center;
    vertical-align: top;
}
.selectedItemName{
    width: 200px;
    height: 40px;
    overflow: hidden;
}