.dropDown{    
    top: 14px;
    right: 100px;
    color: white;
    height: 40px;
    width: 200px;
    background-color: rgb(40, 40, 40)   ;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
    margin: 0 10px;
    z-index: 10;
    display: inline-block;
}
.dropDown input{
    height: 100%;
    width: 80%;
}
.listsContainer{
    max-height: 90vh;
    overflow-x: visible;
    overflow-y: auto;    
    position: absolute;
}
.dropDownItem{
    height: 40px;
    width: 200px;
    background-color: rgb(23, 23, 23);
    line-height: 40px;
    box-sizing: border-box;
    position: relative;
    /* display: none; */
    height: 0px;
    filter: opacity(0);
    transition: 500ms;
    overflow: hidden;
}
.dropDown:hover .dropDownItem{    
    height: 40px;
    filter: opacity(1);
}
.dropDownItem input{

    display: inline-block;
}
.hideItems:hover .dropDownItem{
    display: none;
}
input{
    outline: none;
    border: none;
    background-color: rgba(169, 169, 169, 0.455);
    color: white;
    padding: 0 10px;
    box-sizing: border-box;
    height: 100%;
}
.addButton{
    width: 20%;
    height: 100%;
    display: flex;
    justify-content: center;
    background-color: rgba(169, 169, 169, 0.682);
    display: inline-block;
}
.addButton:hover{
    background-color: green;
    background-color: rgba(255, 254, 254, 0.682);
    box-shadow: 0 0 10px white;
}
.dropDownItem:hover{
    background-color: rgb(61, 61, 61);
}
.dropDownItem:hover .listItem{
    display: inline;
    height: auto;
}
.listItem{
    position: absolute;
    /* display: none; */
    left: -160px;
    bottom: 0;
    background-color: blue;
}
