.pageRangeSelector{
    display: inline-block;
}
.pageRangeText{
    display: inline-block;
    user-select: none;
    color: white;
}
.pageRangeSelector input{
    width: 40px;
    text-align: center;
    margin: 0 10px;
    background-color: rgba(128, 128, 128, 0.199);
}
.pageRangeSelector .button{
    user-select: none;
    display: inline-block;
    border: 1px solid rgba(153, 153, 153, 0.616);
    color: white;
    transition: 500ms;
    font-size: 14px;
    padding: 1px 10px 1px 10px;
    margin: 3px;
    border-radius: 10px;
    cursor: pointer;
    height: 20px;
    vertical-align: top;
}
.pageRangeSelector .button:hover{
    background-color: gray;
}