.authMenu{ 
    background-color: #101010;
    border-radius: 5px;
    position: fixed;
    top: calc(50vh - 200px);    
    width: 450px;    
    left: calc(50vw - 225px);
    color: white;
    box-sizing: border-box;
    text-align: center;
    padding-bottom: 10px;
    box-shadow: 0 4px 8px black;
}
.authMenu input{
    height: 32px;
    color: black;
    background-image: linear-gradient(white, rgb(209, 209, 209));
    box-sizing: border-box;
    border-radius: 2px;
    margin: 4px;    
    padding: 0 10px;
    width: 80%;
    outline: none;
    border: none;   
    text-align: center;     
}
.authTitle{
    padding: 10px;
    font-size: 26px;
    line-height: 26px;
}
.authMenu button{
    width: 80%;
    height: 34px;
    margin-top: 10px;
    background-color: rgb(255, 114, 95);
    background-image: linear-gradient(rgba(255, 0, 0, 0), red);
    color: white;
    font-size: 12px;
    border: none;
    cursor: pointer;
    border-radius: 2px;
    transition: 500ms;
}
.authMenu button:hover{
    background-color: rgb(255, 142, 127);
    box-shadow: 0 0 20px rgb(255, 51, 24);
}
.hl{
    width: 90%;
    height: 2px;
    background-color: rgba(128, 128, 128, 0.521);
    display: inline-block;
    margin-top: 20px;
}
.loginBottomText{
    padding: 10px;
}
.authMenu a{
    color: rgb(175, 175, 238);
    padding-left: 10px;
    cursor: pointer;
}
.errorMessage{
    padding-bottom: 5px;
    color: rgb(224, 134, 134);
}
.inputIcon{
    height: 30px;
    width: 30px;
    display: inline-block;
    position: absolute;
    top: 4px;
    cursor: pointer;
}
.inputIcon img{
    height: 100%;
    width: 100%;
}
.favColorInput{
    position: relative;
    display: inline-block;
    width: 80%;
    text-align: left;
}
.favColorInput input{
    width: calc(100% - 30px);    
    margin-left: 0;
}