.home{
    padding-top: 80px;
}
.topBar{    
    min-height: 45px;
    background-color: #151515;
    border-radius: 10px;    
    display: inline-block;
    margin: 30px 0 10px 0;
    color: white;
    font-size: 20px;
    padding: 15px 20px 10px 20px;
    box-sizing: border-box;   
    width: 100%; 
    text-align: left;  
    position: relative;  
}
.topBarDescription{
    font-size: 14px;
    margin: 10px 0 5px 0;
}
.topBarIcon{
    display: inline-block;
    min-height: 80px;
    width: 80px;
    height: 80px;
    border-radius: 50%;
    overflow: hidden;
    border: 2px solid rgba(255, 255, 255, 0.603);
    background-color: rgba(255, 0, 0, 0.493);
    box-sizing: border-box;
}
.topBarIcon img{
    height: 100%;
    width: 100%;
    object-fit: cover;
}
.topBarTextInfo{
    display: inline-block;    
    width: 80%;
    margin-left: 10px;
}
.userName{
    display: inline-block;
}
.userKarma{
    font-size: 16px;
    color: rgba(255, 255, 255, 0.479);
}
.homeContainer{
    display: inline-block;
}
.homeLine{    
    height: 1px;
    background-color: rgba(0, 0, 0, 0.7);
    display: inline-block;
    margin: 10px 0;
    width: 100%;
}
.homeButtons{
    text-align: left;
    margin-bottom: 10px;
    font-size: 16px;
    position: absolute;
    top: 10px;
    right: 20px;
}
.homeButtons div{
    display: inline-block;
    padding: 0px 20px;
    height: 30px;
    line-height: 26px;
    box-sizing: border-box;
    margin: 5px;
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.5);
    color: white;    
    cursor: pointer;
    transition: 500ms;
}
.homeButtons div:hover{
    background-color: rgba(128, 128, 128, 0.5);
    background-color: rgba(128, 128, 128, 0.164);
}
.homeButtons div img{
    height: 100%;
    width: 100%;
    object-fit: contain;
}
.highlightedHomeButton{
    background-color: rgba(128, 128, 128, 0.5);
}
.spacer{
    width: 10px;
    height: 20px;    
    display: inline-block;
}
.homeRedditButton{
    height: 30px;
    width: 30px;
    display: inline-block;
    vertical-align: bottom;
    padding: 0 10px;
    cursor: pointer;
}
.homeRedditButton img{
    height: 100%;
    width: 100%;
    object-fit: contain;
}
/* 
  >1600 75% + 40 (three columns)
  1400 to 1000 90% + 20 (two)
  1000 to 800 80% + 20 (two)
  700 to 800 one column at 80% (one)
  under 700 1 column at 90% (one)
*/
@media screen and (max-width: 700px){
    .homeContainer{
        width: 90%;
    }
    .column{
        width: 100%;
        margin: 0;
    }
}
@media screen and (max-width: 800px) and (min-width: 700px){
    .homeContainer{
        width: 80%;
    }
    /* There is 1 column and it should take up the whole width*/
    .column{
        width: 100%;        
    }
}
@media screen and (max-width: 1000px) and (min-width: 800px){
    .homeContainer{
        width: calc(90%);
    }
    /* There are 2 columns and 2 spacers, each is a half of the width - 1/2 of the spacer width */
    .column{
        width: calc(50% - 5px);    
        margin: 0px;    
    }
}
@media screen and (max-width: 1600px) and (min-width: 1000px){
    .homeContainer{
        width: calc(80%);
    }
    /* There are 2 columns and 2 spacers, each is a half of the width - 1/2 of the spacer width */
    .column{
        width: calc(50% - 5px);    
        margin: 0px;    
    }
}
@media screen and (min-width: 1600px){
    .homeContainer{
        width: calc(75%);
    }
    /* There are 3 columns and 2 spacers, each is a thrird of the width - 1/3 of the spacer width */
    .column{
        width: calc(33.3% - 7px);        
        margin: 0px;
    }
}
