.blobTest{
    margin-top: 100px;
    min-height: 300px;
    background-color: gray
  }
  .linkTester{
    position: fixed;
    width: 500px;
    height: 500px;
    top: 100px;
    left: calc(50vw - 250px);
    background-color: gray;
    padding: 40px;
    box-sizing: border-box;
  }
  .linkTester iframe{
    height: 100%;
    width: 100%;
  }
  .linkTester video{
    height: 100%;
    width: 100%;
    min-height: 200px;
    background-color: darkgray;
  }
  .linkTester img{
    height: 100%;
    width: 100%;
    object-fit: contain;
  }
