.starBox{
    position: relative;
    height: 26px;
}
.ratingStar{
    height: 20px;
    width: 20px;
    margin: 2px 5px -2px 5px;
    margin: 2px 5px 0px 5px;
    display: inline-block;
}
.ratingStar img{
    height: 100%;
    width: 100%;
}
.hoverStarDescription{
    position: absolute;
    margin-left: 5px;
    margin-top: 2px;
    vertical-align: top;
    top: 35px;
    left: 0;
    width: 160px;
    height: 20px;
}