.linkPreview{
    display: inline-block;
    max-width: 200px;
    max-height: 400px;
    box-sizing: border-box;
    box-shadow: 0 5px 10px black;
    padding: 5px;
    margin: 10px;
    border-radius: 5px;
    cursor: pointer;
    position: relative;    
}
.linkPreview:hover{
    box-shadow: 0 0px 10px rgba(255, 255, 255, 0.479);
}
.linkPreviewImg{
    height: 200px;
    width: 100%;
    object-fit: contain;
}
.title{
    color: white;
    font-size: 16px;
    overflow: hidden;
    display: inline-block;
    overflow: hidden;
    max-width: 100%;
}
.settingsGear{
    display: inline-block;
    height: 20px;
    width: 20px;
    margin: 5px;
    border-radius: 50%;
    position: relative;
}
.settingsGear:hover{
    background-color: rgba(255, 255, 255, 0.37);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.507);
}
.settingsGear img{
    height: 100%;
    width: 100%;
}
.settingsGearHover{
    display: none;
    filter: opacity(0);
    position: absolute;
    left: 15px;
    min-width: 200px;
    background-color: gray;
    z-index: 4;
    border-radius: 5px;
}
.settingsGear:hover .settingsGearHover{
    display: inline;
    filter: opacity(1);
}
.settingsGearHover div{
    padding: 5px;
}
.settingsGearHover div:hover{
    background-color: white;
}