.post{
    margin: 5px 0px;    
    background-color: #151515;  
    color: white;    
    min-height: 200px;
    width: 100%;
    display: inline-block;
    vertical-align: top;
    position: relative;
    box-sizing: border-box;
    overflow-x: hidden;    
    border-radius: 3px;        
    box-shadow: 0 4px 8px black;
    transition: 500ms;
    border: 1px solid rgba(0, 0, 0, 0.5);
    text-align: center;    
}
.post:hover{
    box-shadow: 0 0 8px rgba(255, 255, 255, 0.5);        
    /* transform: scale(1.005); */
}
.postButton img{
    height: 100%;
    width: 100%;
    object-fit: contain;
}
.playButton img{
    height: 100%;
    width: 100%;
    object-fit: contain;
}
.postTextTitle{
    position: absolute;
    bottom: 10px;
    left: 5px;        
    width: calc(100% + 10px);
    filter: opacity(0.5);
    transition: 500ms;    
    color: white;
    z-index: 2;   
    margin: -10px;   
    padding: 0 5px;
    box-sizing: border-box;
}
.postTextTitle:hover{ 
    filter: opacity(1);
    background-color: rgba(128, 128, 128, 0.7);        
    box-shadow: 0 -10px 5px rgba(128, 128, 128, .7);
}
.postTitle{    
    margin-bottom: 5px;
}
.postText{    
    font-size: 12px;
    margin-bottom: 5px;
}
.post img{    
    width: 100%;
    object-fit: contain;    
    margin-bottom: -4px;
}
.postButtons{
    padding-bottom: 5px;
    position: absolute;
    top: 5px;
    width: 100%;
    z-index: 2;
}
.postButton{
    display: inline-block;
    border: 1px solid rgba(153, 153, 153, 0.616);
    color: white;
    transition: 500ms;
    font-size: 14px;
    padding: 1px 10px 1px 10px;
    margin: 3px;
    border-radius: 10px;
    cursor: pointer;
    height: 20px;
    vertical-align: top;
    filter: opacity(0.5);
    transition: 500ms;

}
.postButton:hover{
    background-color: rgba(128, 128, 128, 0.5);
    filter: opacity(1);
}
.playButton{
    height: 100px;
    left: calc(50% - 50px);
    position: absolute;
    top: calc(50% - 50px);
    width: 100px;
}
.test{
    min-height: 20px;
    background-color: blue;
}
.textViewer{
    white-space: pre-wrap;       /* Since CSS 2.1 */
    white-space: -moz-pre-wrap;  /* Mozilla, since 1999 */
    white-space: -pre-wrap;      /* Opera 4-6 */
    white-space: -o-pre-wrap;    /* Opera 7 */
    /* word-wrap: break-word;       Internet Explorer 5.5+ */
    padding: 30px 10px 60px 10px;
}
.textViewerTitle{
    padding: 10px 0 10px 0;
}
.loadingPost{
    animation: loadingAnimation 3s infinite;
    min-height: 300px;
    margin-bottom: 20px;
    user-select: none;
    cursor: pointer;
}
@keyframes loadingAnimation {
    0% { background-color: rgb(29, 29, 29);}
    50% { background-color: rgba(161, 155, 155, .5);}
    100% { background-color: rgb(29, 29, 29);}
}