.navBar{
    opacity: 1;
    padding: 10px;
    position: fixed;
    top: 0;
    transition: .5s;
    width: calc(100% - 20px);    
    background-color: #111;
    box-shadow: 0 3px 5px black;
    z-index: 5;
}
.logo{
    float: left;
    width: 120px;
    cursor: pointer;
    transition: 500ms;
    padding: 5px;
    box-sizing: border-box;
    margin-left: 20px;
}
.logo:hover{
    transform: scale(1.08);
}
.avatar{
    float: right;
    width: 40px;
    cursor: pointer;
    margin-top: 2px;
    transition: 500ms;
    vertical-align: top;
    margin-top: -0.5px;
    background-color: slateblue;
}
.avatar:hover{
    transform: scale(1.08);
}
.opacityZero{
    opacity: 0;
}
.displayHidden{
    display: none;
}
.topNavButtons{
    position: absolute;
    right: 60px;    
}
.addToList{    
    background-color: rgb(40, 40, 40);
    height: 40px;
    width: 40px;
    border-radius: 50%;
    cursor: pointer;
    font-size: 30px;
    color: white;
    text-align: center;
    line-height: 34px;
    display: inline-block;
    vertical-align: top;
    transition: 500ms;
}
.addToList img{
    height: 70%;
    width: 70%;
    margin-top: 15%;    
}
.addToList:hover{
    background-color: rgb(101, 101, 101);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.658);
}
.navButton{
    background-color: rgb(40, 40, 40);
    height: 40px;    
    border-radius: 10px;
    margin: 0 10px;
    padding: 0 10px;
    box-sizing: border-box;
    cursor: pointer;    
    color: white;
    text-align: center;
    line-height: 38px;
    display: inline-block;
    vertical-align: top;
    transition: 500ms;
}
.navButton:hover{
    background-color: rgb(101, 101, 101);
    box-shadow: 0 0 10px rgba(255, 255, 255, 0.658);
}